body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.d-flex {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
  flex-direction: row;
}

.center {
  align-items: center;
  display: flex;
  justify-content: center;
}
.btn {
  display: inline-block;
  margin-bottom: 0;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  border-radius: 4px;
  user-select: none;
}
.btn-primary {
  color: #fff;
  background-color: #337ab7;
  border-color: #2e6da4;
}
.btn-primary:hover {
  background-color: #286090;
  border-color: #204d74;
}
.btn-danger {
  color: #fff;
  background-color: #d9534f;
  border-color: #d43f3a;
}
.btn-danger:hover {
  background-color: #c9302c;
  border-color: #ac2925;
}

.p-3 {
  padding: 1rem;
}

.ml-2 {
  margin-left: 0.5rem;
}

.justify-content-center {
  display: flex;
  justify-content: center;
}

.selected-panel {
  background-color: grey !important;
  border: 1px solid lightgrey;
  color: white;
}

.solar-box-border {
  border: 1px solid lightgrey;
}

.solar-box {
  display: table;
  height: 25px;
  width: 25px;
}

.edit-design:hover {
  background-color: #d1d1d1;
}
th{
  text-align : center;    
}
body {
  background: #2b709f;
}
